'use strict';
import React from 'react';
import ReactDOM from 'react-dom';
import { default as __links } from 'sources/links';

require('styles/pages/contact.less');

const Link = React.createClass({
  render() {
    return (
      <li className="link-item list-item-icon">
        <div className="list-item-icon__icon">
          <a href={ this.props.url } target="_blank">
            <i className={`icon ${this.props.icon}`}></i>
          </a>
        </div>
      </li>
    );
  }
});


export default React.createClass({
  getInitialState() {
    return {
      links: __links,
      backdrop: require( 'images/site/banana-plants.png')
    };
  },

  componentDidMount() {
    this.setState({
      links: __links
    });

    console.log(this.img);
    console.log(this.state.backdrop);

  },
  render() {
    return (
      <div ref={ (img) => this.img = this.state.backdrop } className="page-wrapper">
        <ul  className="link-items list-icons animated fadeIn">
          { this.state.links.map((result, i) => {
            return <Link key={ result.name } url={ result.url } icon={ result.icon } />;
          }) }
        </ul>
      </div>
    );
  }
});


